// R3 - Pending changes

import React from 'react'
import * as route from 'utils/routes'

import DashboardIcon from '@material-ui/icons/Dashboard'
import AssignmentIcon from '@material-ui/icons/Assignment'
import PersonIcon from '@material-ui/icons/Person'
import SecurityIcon from '@material-ui/icons/Security'
import GroupIcon from '@material-ui/icons/Group'
import WorkIcon from '@material-ui/icons/Work'
import PaletteIcon from '@material-ui/icons/Palette'
import BookIcon from '@material-ui/icons/Book'
import PlayIcon from '@material-ui/icons/PlayCircleFilled'
import GamepadIcon from '@material-ui/icons/VideogameAsset'
import ForumIcon from '@material-ui/icons/Forum'

const navigatorStruct = [
  {
    id: 'Principal',
    children: [
      {
        id: 'Dashboard',
        uri: route.admRoutes.homeDashboard,
        icon: <DashboardIcon />,
        disabled: false,
      },
      {
        id: 'Curriculum',
        uri: '/any',
        icon: <AssignmentIcon />,
        disabled: true,
      },
    ],
  },
  {
    id: 'Contas',
    children: [
      {
        id: 'Alunos',
        uri: route.admRoutes.studentManager,
        icon: <PersonIcon />,
        disabled: false,
      },
      {
        id: 'Administradores',
        uri: route.admRoutes.adminManager,
        icon: <SecurityIcon />,
        disabled: false,
      },
      {
        id: 'Empresas',
        uri: route.admRoutes.companyManager,
        icon: <GroupIcon />,
        disabled: false,
      },
    ],
  },
  {
    id: 'Conteúdo',
    children: [
      {
        id: 'Vagas de emprego',
        uri: route.admRoutes.jobs,
        icon: <WorkIcon />,
        disabled: false,
      },
      {
        id: 'Eventos culturais',
        uri: route.admRoutes.culture,
        icon: <PaletteIcon />,
        disabled: false,
      },
      {
        id: 'Cursos',
        uri: route.admRoutes.courses,
        icon: <BookIcon />,
        disabled: false,
      },
      {
        id: 'Videoaulas',
        uri: route.admRoutes.videolessons,
        icon: <PlayIcon />,
        disabled: false,
      },
      {
        id: 'Jogos',
        uri: '/any',
        icon: <GamepadIcon />,
        disabled: true,
      },
      {
        id: 'Fórum',
        uri: '/any',
        icon: <ForumIcon />,
        disabled: true,
      },
    ],
  },
]

// Make this with pure functions later
export default (activeItem = '') => {
  try {
    for (let i = 0; i < navigatorStruct.length; i++) {
      for (let j = 0; j < navigatorStruct[i].children.length; j++) {
        if (activeItem === navigatorStruct[i].children[j].id) {
          navigatorStruct[i].children[j].active = true
        } else {
          navigatorStruct[i].children[j].active = false
        }
      }
    }

    return navigatorStruct
  } catch (err) {
    console.error(err)
    alert('Erro na estruturação do menu.\nContate o administrador.')
  }
}
