import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'

import { Switch, Route, withRouter as _withRouter } from 'react-router-dom'
import { compose } from 'recompose'
import { connect } from 'react-redux'

import Login from 'pages/Login/Form'
import LostCredentials from 'pages/Login/LostCredentials'
import ReadMore from 'pages/Login/ReadMore'
import Dashboard from 'pages/Dashboard'
import Student from 'pages/Accounts/Student'
import Admin from 'pages/Accounts/Admin'
import Company from 'pages/Accounts/Company'
import Jobs from 'pages/Content/Jobs'
import Culture from 'pages/Content/Culture'
import Courses from 'pages/Content/Courses'
import Videolessons from 'pages/Content/Videolessons'
import Redirect from 'components/Redirect'

import { admRoutes, loginRoutes } from 'utils/routes'

const Logged = () => (
  <Redirect to={admRoutes.homeDashboard} timer={1600} />
)

const NotLogged = () => (
  <Redirect to={loginRoutes.loginForm} timer={1600} />
)

const App = ({ auth }) => {
  const [login, setLogin] = useState(false)

  useEffect(() => {
    setLogin(auth.login)
  }, [auth])

  return (
    <Switch>
      {/* UNPROTECTED ROUTES */}
      <Route component={(login ? Logged : Login)} exact path={loginRoutes.loginForm} />
      <Route component={login ? Logged : LostCredentials} exact path={loginRoutes.lostCredentials} />
      <Route component={login ? Logged : ReadMore} exact path={loginRoutes.readMore} />

      {/* PROTECTED ROUTES */}
      <Route component={login ? Dashboard : NotLogged} exact path={admRoutes.homeDashboard} />
      <Route component={login ? Student : NotLogged} exact path={admRoutes.studentManager} />
      <Route component={login ? Admin : NotLogged} exact path={admRoutes.adminManager} />
      <Route component={login ? Company : NotLogged} exact path={admRoutes.companyManager} />
      <Route component={login ? Jobs : NotLogged} exact path={admRoutes.jobs} />
      <Route component={login ? Culture : NotLogged} exact path={admRoutes.culture} />
      <Route component={login ? Courses : NotLogged} exact path={admRoutes.courses} />
      <Route component={login ? Videolessons : NotLogged} exact path={admRoutes.videolessons} />

      {/* 404 */}
      <Route component={NotLogged} />
    </Switch>
  )
}

App.propTypes = {
  auth: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
}

const mapStateToProps = ({ auth }) => ({ auth })

const withRouter = () => _withRouter

export default compose(
  withRouter(),
  connect(mapStateToProps),
)(App)
